.nav-item a , .nav-item a:hover{
    color: rgb(167, 167, 167) !important;
}
.nav-link {
    display: block;
    padding: .5rem 0rem;
}
.createBiography .nav-pills .nav-link.active,
.createBiography .nav-pills .show>.nav-link{
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.09) 0px 2px 30px 0px, rgba(0, 0, 0, 0.09) 0px 0px 80px 0px !important;
    border: none;
    color: #E85A19 !important;
    border-left: 3px solid #E85A19;
    transition: 1s;
}
@media only screen and  (max-width:414px){
    .container-fluid-responsive{
        margin: 0 !important;
        padding: 0!important;
    }
}