
.video-custom-container .container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .video-custom-container .video-container {    
    border-radius: 4px;
    margin: 0 auto;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  .video-custom-container video {
    width: 100%;
    height: 100%;
    border-radius: 4px;
  }
  
  .video-custom-container .video-controls {
    right: 0;
    left: 0;
    padding: 5px;
    position: absolute;
    bottom: 0;
    transition: all 0.2s ease;
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.5));
    color: white;
  }
  
  .video-custom-container .video-controls.hide {
    opacity: 0;
    pointer-events: none;
  }
  
 .video-custom-container  .video-progress {
    position: relative;
  }
  
  .video-custom-container progress {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border-radius: 2px;
    width: 100%;
    height: 8.4px;
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
  }
  
  .video-custom-container progress::-webkit-progress-bar {
    background-color: #474545;
    border-radius: 2px;
  }
  
  .video-custom-container progress::-webkit-progress-value {
    background: #ff0000 !important;
    border-radius: 2px;
  }
  
  .video-custom-container progress::-moz-progress-bar {
    border: 1px solid #ff0000 !important;
    background: #ff0000 !important;
  }
  
 .video-custom-container  .seek {
    position: absolute;
    top: 0;
    width: 100%;
    cursor: pointer;
    margin: 0;
    left: 0;
  }
  
  .video-custom-container .seek:hover+.seek-tooltip {
    display: block;
  }
  
  .video-custom-container .seek-tooltip {
    display: none;
    position: absolute;
    top: -50px;
    margin-left: -20px;
    font-size: 12px;
    padding: 3px;
    content: attr(data-title);
    font-weight: bold;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.6);
  }
  
 .video-custom-container  .bottom-controls {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .video-custom-container .left-controls {
    display: flex;
    align-items: center;
    color: #fff;
  }
  
  .video-custom-container .volume-controls {
    display: flex;
    align-items: center;
    margin-right: 10px;
  }
  
  .video-custom-container .volume-controls input {
    width: 100px;
    opacity: 1;
    transition: all 0.4s ease;
  }
  
  .video-custom-container .volume-controls:hover input, .volume-controls input:focus {
    width: 100px;
    opacity: 1;
  }
  
 .video-custom-container  button {
    cursor: pointer;
    position: relative;
    margin-right: 7px;
    font-size: 12px;
    padding: 3px;
    border: none;
    outline: none;
    background-color: transparent;
  }
  
  /* .video-custom-container button * {
    pointer-events: none;
  } */
  
  .video-custom-container button::before {
    content: attr(data-title);
    position: absolute;
    display: none;
    right: 0;
    top: -50px;
    background-color: rgba(0, 0, 0, 0.6);
    color: #fff;
    font-weight: bold;
    padding: 4px 6px;
    word-break: keep-all;
    white-space: pre;
  }
  
  .video-custom-container button:hover::before {
    display: inline-block;
  }
  
  .video-custom-container .fullscreen-button {
    margin-right: 0;
  }
  
  .video-custom-container .pip-button svg {
    width: 26px;
    height: 26px;
  }
  
  .video-custom-container  .playback-animation {
    pointer-events: none;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -40px;
    margin-top: -40px;
    width: 80px;
    height: 80px;
    border-radius: 80px;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
  }
  
  .video-custom-container input[type=range] {
    -webkit-appearance: none;
    -moz-appearance: none;
    height: 8.4px;
    background: transparent;
    cursor: pointer;
  }
  
.video-custom-container input[type=range]:focus {
    outline: none;
  }
  
  .video-custom-container  input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    cursor: pointer;
    border-radius: 1.3px;
    -webkit-appearance: none;
    transition: all 0.4s ease;
  }
  
  .video-custom-container  input[type=range]::-webkit-slider-thumb {
    height: 16px;
    width: 16px;
    border-radius: 16px;
    background: #ff0000 !important;
    cursor: pointer;
    -webkit-appearance: none;
    margin-left: -1px;
  }
  
  .video-custom-container   input[type=range]:focus::-webkit-slider-runnable-track {
    background: transparent;
  }
  
  .video-custom-container  input[type=range].volume {
    height: 5px;
    background-color: #fff;
  }
  
  .video-custom-container input[type=range].volume::-webkit-slider-runnable-track {
    background-color: transparent;
  }
  
  .video-custom-container input[type=range].volume::-webkit-slider-thumb {
    margin-left: 0;
    height: 14px;
    width: 14px;
    background: #fff;
  }
  
  .video-custom-container  input[type=range]::-moz-range-track {
    width: 100%;
    height: 8.4px;
    cursor: pointer;
    border: 1px solid transparent;
    background: transparent;
    border-radius: 1.3px;
  }
  
  .video-custom-container  input[type=range]::-moz-range-thumb {
    height: 14px;
    width: 14px;
    border-radius: 50%;
    border: 1px solid #ff0000 !important;
    background: #ff0000 !important;
    cursor: pointer;
    margin-top: 5px;
  }
  
  .video-custom-container  input[type=range]:focus::-moz-range-track {
    outline: none;
  }
  
  .video-custom-container  input[type=range].volume::-moz-range-thumb {
    border: 1px solid #fff;
    background: #fff;
  }
  
  .video-custom-container  .hidden {
    display: none;
  }
  
  .video-custom-container  svg {
    width: 28px;
    height: 28px;
    fill: #fff;
    stroke: #fff;
    cursor: pointer;
  }
  /* brogress bar */
.video-custom-container input[type=range]::-webkit-slider-runnable-track {
    width: 700px;
    height: 4px;
    background: unset;
    border: none;
    border-radius: 3px;
}
.video-custom-container input[type=range]::-moz-range-track {
    background: unset;
    border: none;
    border-radius: 3px;
}

.video-custom-container input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    border: 2px solid #ff0000;
    height: 16px;
    width: 10px;
    border-radius: 40%;
    background: white;
    margin-top: -6px;
}
.video-custom-container input[type=range]::-moz-range-thumb {
    -webkit-appearance: none;
    border: 2px solid #ff0000;
    height: 16px;
    width: 16px;
    border-radius: 50%;
    background: white;
    margin-top: -5px;
}
.video-custom-container input[type="range" i]::-webkit-slider-container {
    border-radius: 20px !important;
}
.video-custom-container .progress-container{
  position: relative;
  width: 100%;
}