    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
  } */
.video-container{
    position: relative;
    width: auto;
}
.video-container #play{
    cursor: pointer;
}
.video-container video::-webkit-media-controls-mute-button:parent {
    background-color: yellow !important;
}
.video-container video::-webkit-media-controls-timeline {
    display:block;
    background-color: transparent;
    width: 76%;
    position: absolute;
    right: 6.5rem;
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
}
.video-container video::-webkit-media-controls-volume-slider::-webkit-media-slider-container, 
.video-container video::-webkit-media-controls-timeline::-webkit-media-slider-container {
	border: 0px;
	border-radius: 5px;
    cursor: pointer;  
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
}
.video-container video::-webkit-media-controls-volume-slider-container,
.video-container video::-webkit-media-controls-volume-slider,
.video-container video::-webkit-media-controls-overflow-button,
.video-container video::-webkit-media-controls-fullscreen-button,
.video-container video::-webkit-media-controls-play-button,
.video-container video::-webkit-media-controls-mute-button,
.video-container video::-webkit-media-controls-mute-button:hover,
.video-container ::-webkit-media-slider-thumb 
{
    display:none;
    -webkit-appearance: none;
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
}
.video-container video::-webkit-media-controls-current-time-display{
	font-family: 'Lato', sans-serif;
	font-size: 13px;
    font-weight: normal; 
    position: absolute;
    right: 5rem;
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
}
.video-container video::-webkit-media-controls-time-remaining-display {
	font-family: 'Lato', sans-serif;
	font-size: 13px;
    font-weight: normal;  
    position: absolute;
    left: 1rem;
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
}
.video-container .circle-light-rec{
    width: 30px;
    height: 30px;
    background-color: #fdfdfd6b;
    text-align: center;
    border-radius: 50%;
    position: absolute;
    bottom: 0.3rem;
    left: 0.3rem;
}
.video-container .circle-light-play{
    width: 30px;
    height: 30px;
    background-color: #fdfdfd6b;
    text-align: center;
    border-radius: 50%;
    position: absolute;
    bottom: 0.2rem;
    left: 2.3rem;
    padding-right: 0.3rem;
}
.video-container .rec{
    width:18px;
    height: 18px;
    border-radius: 50%;
    background-color: #f40000;
    transition: 1s;
   
}
.video-container .stop{
    border-radius: 1px;
    width:15px;
    height: 15px;
    background-color: #f40000;
    transition: 1s;
    left: 8px !important;
    top: 8px !important;
}
.video-container .btns-group{
    position: absolute;
    bottom: 0.9rem;
    left:0.4rem;
    transition: 2s;
  
}
.video-container .btn-group-rec-style{
    background-color: #04040482;
    width: 16rem;
    height: 2.5rem;
    border-radius: 24px;
    left: 33%; 
    transition: 2s; 
}
.video-container .btn-rec-position{
    position: absolute;
    left: 6px;
    top: 6px;
}
.video-container.btn-play-position{
    position: absolute;
    left: 0.3rem;
    top: 0.3rem;
}
.video-container .start-text{
    position: absolute;
    left: 2.2rem;
    top: 0.3rem;
}
.video-container .recording-text{
    position: absolute;
    left: 2.2rem;
    top: 0.3rem;
}
.video-container .play-text{
    position: absolute;
    left: 4.2rem;
    top: 0.3rem;
}
.video-container .time-limitation{
    position: absolute;
    left : 12rem;
    top: 0.3rem;   
}
.loading{
    position: fixed;
    z-index: 12345;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.473);
    text-align: center !important;
    top: 0px;
    left: 0px;
    padding-top: 15rem;
}

.video-custom-container .container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .video-custom-container .video-container {    
    border-radius: 4px;
    margin: 0 auto;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  .video-custom-container video {
    width: 100%;
    height: 100%;
    border-radius: 4px;
  }
  
  .video-custom-container .video-controls {
    right: 0;
    left: 0;
    padding: 5px;
    position: absolute;
    bottom: 0;
    transition: all 0.2s ease;
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.5));
    color: white;
  }
  
  .video-custom-container .video-controls.hide {
    opacity: 0;
    pointer-events: none;
  }
  
 .video-custom-container  .video-progress {
    position: relative;
  }
  
  .video-custom-container progress {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border-radius: 2px;
    width: 100%;
    height: 8.4px;
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
  }
  
  .video-custom-container progress::-webkit-progress-bar {
    background-color: #474545;
    border-radius: 2px;
  }
  
  .video-custom-container progress::-webkit-progress-value {
    background: #ff0000 !important;
    border-radius: 2px;
  }
  
  .video-custom-container progress::-moz-progress-bar {
    border: 1px solid #ff0000 !important;
    background: #ff0000 !important;
  }
  
 .video-custom-container  .seek {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    cursor: pointer;
    margin: 0;
  }
  
  .video-custom-container .seek:hover+.seek-tooltip {
    display: block;
  }
  
  .video-custom-container .seek-tooltip {
    display: none;
    position: absolute;
    top: -50px;
    margin-left: -20px;
    font-size: 12px;
    padding: 3px;
    content: attr(data-title);
    font-weight: bold;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.6);
  }
  
 .video-custom-container  .bottom-controls {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .video-custom-container .left-controls {
    display: flex;
    align-items: center;
    color: #fff;
  }
  
  .video-custom-container .volume-controls {
    display: flex;
    align-items: center;
    margin-right: 10px;
  }
  
  .video-custom-container .volume-controls input {
    width: 100px;
    opacity: 1;
    transition: all 0.4s ease;
  }
  
  .video-custom-container .volume-controls:hover input, .volume-controls input:focus {
    width: 100px;
    opacity: 1;
  }
  
 .video-custom-container  .playBtn {
    cursor: pointer;
    position: relative;
    margin-right: 7px;
    font-size: 12px;
    padding: 3px;
    border: none;
    outline: none;
    background-color: transparent;
  }
  
  .video-custom-container .playBtn * {
    pointer-events: none;
  }
  
  .video-custom-container .playBtn::before {
    content: attr(data-title);
    position: absolute;
    display: none;
    right: 0;
    top: -50px;
    background-color: rgba(0, 0, 0, 0.6);
    color: #fff;
    font-weight: bold;
    padding: 4px 6px;
    word-break: keep-all;
    white-space: pre;
  }
  
  .video-custom-container button:hover::before {
    display: inline-block;
  }
  
  .video-custom-container .fullscreen-button {
    margin-right: 0;
  }
  
  .video-custom-container .pip-button svg {
    width: 26px;
    height: 26px;
  }
  
  .video-custom-container  .playback-animation {
    pointer-events: none;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -40px;
    margin-top: -40px;
    width: 80px;
    height: 80px;
    border-radius: 80px;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
  }
  
  .video-custom-container input[type=range] {
    -webkit-appearance: none;
    -moz-appearance: none;
    height: 8.4px;
    background: transparent;
    cursor: pointer;
  }
  
.video-custom-container input[type=range]:focus {
    outline: none;
  }
  
  .video-custom-container  input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    cursor: pointer;
    border-radius: 1.3px;
    -webkit-appearance: none;
    transition: all 0.4s ease;
  }
  
  .video-custom-container  input[type=range]::-webkit-slider-thumb {
    height: 16px;
    width: 16px;
    border-radius: 16px;
    background: #ff0000 !important;
    cursor: pointer;
    -webkit-appearance: none;
    margin-left: -1px;
  }
  
  .video-custom-container   input[type=range]:focus::-webkit-slider-runnable-track {
    background: transparent;
  }
  
  .video-custom-container  input[type=range].volume {
    height: 5px;
    background-color: #fff;
  }
  
  .video-custom-container input[type=range].volume::-webkit-slider-runnable-track {
    background-color: transparent;
  }
  
  .video-custom-container input[type=range].volume::-webkit-slider-thumb {
    margin-left: 0;
    height: 14px;
    width: 14px;
    background: #fff;
  }
  
  .video-custom-container  input[type=range]::-moz-range-track {
    width: 100%;
    height: 8.4px;
    cursor: pointer;
    border: 1px solid transparent;
    background: transparent;
    border-radius: 1.3px;
  }
  
  .video-custom-container  input[type=range]::-moz-range-thumb {
    height: 14px;
    width: 14px;
    border-radius: 50%;
    border: 1px solid #ff0000 !important;
    background: #ff0000 !important;
    cursor: pointer;
    margin-top: 5px;
  }
  
  .video-custom-container  input[type=range]:focus::-moz-range-track {
    outline: none;
  }
  
  .video-custom-container  input[type=range].volume::-moz-range-thumb {
    border: 1px solid #fff;
    background: #fff;
  }
  
  .video-custom-container  .hidden {
    display: none;
  }
  
  .video-custom-container  svg {
    width: 28px;
    height: 28px;
    fill: #fff;
    stroke: #fff;
    cursor: pointer;
  }
  /* brogress bar */
.video-custom-container input[type=range]::-webkit-slider-runnable-track {
    width: 700px;
    height: 4px;
    background: unset;
    border: none;
    border-radius: 3px;
}
.video-custom-container input[type=range]::-moz-range-track {
    background: unset;
    border: none;
    border-radius: 3px;
}

.video-custom-container input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    border: 2px solid #ff0000;
    height: 16px;
    width: 10px;
    border-radius: 40%;
    background: white;
    margin-top: -6px;
}
.video-custom-container input[type=range]::-moz-range-thumb {
    -webkit-appearance: none;
    border: 2px solid #ff0000;
    height: 16px;
    width: 16px;
    border-radius: 50%;
    background: white;
    margin-top: -5px;
}
.video-custom-container input[type="range" i]::-webkit-slider-container {
    border-radius: 20px !important;
}
.video-custom-container .progress-container{
  position: relative;
  width: 100%;
}