.overview .p-5 {
    padding: 2.3rem!important;
}
.input-radio{
    display: inline-block;
  }
  input[type=radio] {
      display: none;
    }
    input[type=radio] + label , .custom-radio{
        border : 1px solid #E85A19 !important;
        color : #E85A19 !important;
        background-color: white !important;
        padding: 0.2rem 1.4rem;
        cursor: pointer;
    }
    input[type=radio]:checked + label ,.last-btn:hover{
        background-color: #E85A19 !important;
        color: white !important;
    }
    .first-btn{
        border-radius: 4px 0 0 4px;
        border-right: none !important;
    }
    .last-btn{
        border-radius:0  4px  4px 0;
        border-left: none !important;
    }
.MuiChip-root{
    background-color: #FFE9DD !important;
    color: #E1631B !important;
    border-radius: 4px !important;
    border: none !important;
}
.MuiFormHelperText-contained {
    text-align: end !important;
}
@media only screen and (max-width : 414px){
    .overview .p-5 {
        padding: 0.9rem!important;
    }
    .pt-xs-4{
        padding-top: 2rem;
    }
}
