.pagination-controls__button {
    color: #000;
    display: inline-block;
    width: 30px;
    height: 30px;
    display: inline-block;
    cursor: pointer;
    margin: 0 0.1rem;
    padding: 5px 9px;
    border-radius: 70px;
    transition: 1s;
  }
  pagination-results{
      transition: 1s;
  }
  .pagination-controls__button:first-child {
    margin-left: 0;
  }
  .pagination-controls__button:last-child {
    margin-right: 0;
  }
  .pagination-controls__button:hover:not(:active) {
    transition: transform 0.15s;
    transform: translateY(-1px);
  }
  
  .pagination-controls__button--active , .pagination-controls__button:hover {
    background: #e1631bf5;
    display: inline-block;
    color: #fff;
    transition: 1s;
  }
  .pagination-controls__button--active:hover, .pagination-controls__button--active:hover:not(:active) {
    transform: none;
  }