.overview .offset-3 {
    margin-left: 27%;
}
.dots-btn,.more-btn{
    position: relative;
    border : none ;
    background-color: transparent;
}
.custom-list {
    position: absolute;
    top: 1.6rem;
    background: white;
    border-radius: 5px;
    color: #606060;
    font-size: 14px;
    padding: 0;
    left: 0rem;
    z-index: 1;
}

.custom-list li{
    padding: 0.3rem 1.2rem;
}
.custom-list li:hover{
    background-color: rgba(232, 91, 25, 0.267) !important;
}
.modal-video .modal-content{
    background-color: transparent;
    border: none;
}
.modal-video iframe{
    border-radius: 0px;
    border : none;
}
.modal-content{
    border-radius: 0;
}
