.offset-6 {
    margin-left: 60.333333%;
}
.btn-dots::after{
    content: "";
    background-color: #f2f2f2 !important;
    border-radius: 50%;
    width:20px;
    height: 20px;
}
.card-container {
    border: none;
    width: 100%;
    margin: 0 !important;
    padding: 0;
}
.btn-dots:hover{
    background-color: #f2f2f2 !important;
    border-radius: 5px;
    padding: .2rem 0rem !important;
}
.success{
    position: absolute;
    right: 6px;
    top: 6px;
}
.custom-progressBar div{
    background: transparent linear-gradient(270deg, #F6A025 0%, #F3851C 34%, #E4651A 57%, #E1631C 81%, #F27016 100%) 0% 0% no-repeat padding-box !important;
    
}
.overlay-container{
    position: relative;
}
.overlay{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #00000045;
    transition: 1s;
}
.overlay-container:hover .overlay{

    transition: 1s;
}
@media only screen and  (max-width:1024px){
    .offset-6 {
        margin-left: 50%;
    }
}
@media only screen and  (max-width:414px){
    .btn-group-rec-style {
        left: 15%;
    }
    .offset-6{
        margin-left: 34%;
    }
}
@media only screen and  (max-width:375px){
    .btn-group-rec-style {
        left: 9%;
    }
    .offset-6{
        margin-left: 29%;
    }
}
@media only screen and (max-width: 360px)
{
    .offset-6 {
        margin-left: 25%;
    }
}
@media only screen and  (max-width:320px){
    .btn-group-rec-style {
        left: 3%;
    }
    .offset-6{
        margin-left: 15%;
    }
}




